body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html, body{
  height: 100%;
}
.App {
  text-align: center;
}
.parent{
  margin-bottom: 10px;
  padding: 10px;
}
.sevenColGrid{
  display: grid;
  grid-template-columns: repeat(7,1fr);
  height: 40px;
  grid-gap: 5px;
  gap: 5px;
}

h1 {
  text-align: center;
}

.nav{
  height: 2rem;
  display: flex;
  justify-content: center;
  align-items: center;
}

.buttons{
  position: absolute;
  left: 20px;
}

.cell {
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
    transition: 0.3s;
    width: 100%;
    height: 115px;
    border-radius: 3px;
    display: grid;
    justify-content: center;
  }

  span {
    text-align: center;
  }

  .presentDay{
    background-color: #41B6E6;
  }

.appointment {
    box-shadow: 0 2px 4px 0 rgba(0,0,0,0.2);
    transition: 0.3s;
    width: 150px;
    height: 20px;
    border-radius: 3px;
    display: grid;
    place-items: center;
    justify-content: center;
    text-align: center;
}

.color1{
    background-color: #0bb488 ;
}
.color2{
    background-color: #9cdaca;
}
.color3{
    background-color: #ffab43;
}
.color4{
    background-color: #8b655f;
}
.color5{
    background-color: #ffe843;
}
.color6{
    background-color: #883c77 ;
}
.cellDays {
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
    transition: 0.3s;
    width: 100%;
    height: 40px;
    border-radius: 5px;
    display: grid;
    justify-content: center;
  }
.grayDay{
    background-color: gray;
}
